.Modal {
    position: fixed;
    display: flex;
    flex-flow: row wrap;
    align-items: center;
    z-index: 500;
    background: #DD5E89;  
    background: -webkit-linear-gradient(to right, #F7BB97, #DD5E89); 
    background: linear-gradient(to right, #F7BB97, #DD5E89); 
    width: 70%;
    padding: 16px;
    left: 15%;
    top: 5%;
    border-radius: 15px;
    box-sizing: border-box;
    transition: all 2s ease-in;
    box-shadow: 10px 10px 50px 2px rgba(0,0,0,0.79);
    -webkit-box-shadow: 10px 10px 50px 2px rgba(0,0,0,0.79);
    -moz-box-shadow: 10px 10px 50px 2px rgba(0,0,0,0.79); 
    cursor: default;
}

.closeButton {
    display: inline-block;
    height: 50px;
    width: 100px;
    background-color: red;
    border-radius: 20px;
    border: none;
    margin: 10px;
    font-family: 'Amatic SC Bold';
    font-size: 1em;
    cursor: pointer;
}

.answerButton {
    display: inline-block;
    height: 50px;
    width: 100px;
    background-color: #00cc66;
    border-radius: 20px;
    border: none;
    margin: 10px;
    font-family: 'Amatic SC Bold';
    font-size: 1em;
    cursor: pointer;
}

.header {
    width: 100%;
    margin: auto;
}

.text {
    width: 50%;
    margin: auto;
}

.timer {
    width: 50%;
    margin: auto;
}

.button {
    width: 100%;
    margin: auto;
}

.answer {
    font-size: 1.5em;
}

span {
    font-family: Arial, Helvetica, sans-serif;
}

@media only screen and (min-width: 700px) {
    .Modal {
        top: 20%;
    }
}